<template>
  <div class="play-list">
    <div class="header">
      <div
        class="bg"
        :style="{
          backgroundImage: `url(${
            pl.backgroundCoverUrl ? pl.backgroundCoverUrl : pl.coverImgUrl
          })`,
        }"
      ></div>
      <div class="list-item">
        <div class="pic">
          <img :src="pl.coverImgUrl" />

          <div class="count">
            <img src="../assets/images/itemplay.png" />
            {{ num }}
          </div>
        </div>
        <div class="content">
          <div class="title">{{ pl.name }}</div>
          <div class="creator">
            <div class="img">
              <img :src="creator.avatarUrl" />
            </div>
            <div class="nickname">
              {{ creator.nickname }}
            </div>
          </div>
          <div class="desc">
            {{ pl.description }}
          </div>
        </div>
      </div>
    </div>
    <ul class="songs">
      <SongItem
        v-for="(song, index) in songs"
        :key="song.id"
        :song="song"
        :index="index"
        @list-update-playlist="updatePl"
      ></SongItem>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
import SongItem from "@/components/SongItem.vue";

export default {
  name: "PlayList",
  components: {
    SongItem,
  },
  data() {
    return {
      pl: [],
      ids: [],
      songs: [],
      creator: [],
    };
  },
  computed: {
    ...mapState(["currentmusic", "paused"]),
    num() {
      var n = this.pl.playCount;
      if (n > 100000000) {
        n = (n / 100000000).toFixed(2) + "亿";
      } else if (n > 10000) {
        n = (n / 10000).toFixed(2) + "万";
      }
      return n;
    },
  },
  created() {
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
    });

    this.axios
      .get(`/playlist/detail?id=${this.$route.query.id}`)
      .then((response) => {
        this.pl = response.data.playlist;
        this.creator = this.pl.creator;

        this.ids = response.data.playlist.trackIds.map(function (item) {
          return item.id;
        });
        this.ids = this.ids.length > 20 ? this.ids.slice(0, 20) : this.ids;

        this.ids = this.ids.join(",");
        this.axios.get(`/song/detail?ids=${this.ids}`).then((response) => {
          this.songs = response.data.songs;

          this.$toast.clear();
        });
      })
      .catch(() => {
        this.$toast.clear();
      });
  },
  activated() {
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
    });

    this.axios
      .get(`/playlist/detail?id=${this.$route.query.id}`)
      .then((response) => {
        this.pl = response.data.playlist;
        this.creator = this.pl.creator;

        this.ids = response.data.playlist.trackIds.map(function (item) {
          return item.id;
        });
        this.ids = this.ids.length > 20 ? this.ids.slice(0, 20) : this.ids;

        this.ids = this.ids.join(",");
        this.axios.get(`/song/detail?ids=${this.ids}`).then((response) => {
          this.songs = response.data.songs;

          this.$toast.clear();
        });
      })
      .catch(() => {
        this.$toast.clear();
      });
  },
  methods: {
    updatePl() {
      this.songs = this.songs.map((element) => {
        if (!element.song) {
          element.song = {};
          element.song.artists = element.ar;
        }
        return element;
      });

      this.$store.commit("updatePlaylist", this.songs);
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      // console.log("beforeRouteEnter");
      vm.$store.commit("updateActiveid", 1);
    });
  },
};
</script>

<style lang="less" scoped>
.play-list {
  background-color: #fff;
  margin: 86px 0 50px 0;
  .header {
    position: relative;
    overflow: hidden;
    .bg {
      width: 100%;
      height: 50vw;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      filter: blur(20px);
    }
    .list-item {
      position: absolute;
      top: 50px;
      left: 15px;
      color: #fff;
      display: flex;
      .pic {
        width: 30vw;
        position: relative;
        border-radius: 8px;
        overflow: hidden;
        img {
          display: block;
          width: 100%;
        }
        .count {
          font-size: 12px;
          position: absolute;
          color: #fff;
          top: 0;
          right: 0;
          background-color: rgba(153, 153, 153, 0.5);
          border-radius: 10px;
          padding: 0 6px;
          margin-top: 3px;
          margin-right: 3px;
          display: flex;
          align-items: center;
          img {
            width: 14px;
            display: inline-block;
            margin-right: 2px;
          }
        }
      }
      .content {
        flex: 1;
        margin: 0 30px;
        .creator {
          display: flex;
          align-items: center;
          margin: 8px 0;
          .img {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            overflow: hidden;
            img {
              display: block;
              width: 100%;
            }
          }
          .nickname {
            color: #ddd;
            flex: 1;
            font-size: 12px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
            margin-left: 3px;
          }
        }
        .desc {
          font-size: 12px;
          color: #ddd;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
    }
  }
  .player {
    bottom: 0;
  }
}
</style>